body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}


.react-autosuggest__container {
  position: relative;
  width: 100%;
}

.react-autosuggest__input {
  width: 100%;
  height: 40px;
  padding: 10px 20px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: 1px solid #aaaaaa4a;
  border-radius: 4px;
  background-color: #f1f3f4;
}

.react-autosuggest__input--focused {
  outline: none;
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  box-shadow: 0 1rem 3em rgba(0, 0, 0, .35) !important;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  top: 40px;
  width: 100%;
  border: 1px solid #aaaaaa4a;
  background-color: #fff;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  box-shadow: 0 .6rem 1.5rem rgba(0, 0, 0, .15) !important;
  z-index: 9999;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 10px 20px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd;
}

.react-autosuggest__section-container {
  border-top: 1px dashed #ccc;
}

.react-autosuggest__section-container--first {
  border-top: 0;
}

.react-autosuggest__section-title {
  padding: 10px 0 0 10px;
  font-size: 12px;
  color: #777;
}

span.react-autosuggest__item {
  font-size: 12px;
}

/* Framework changes*/
.nav-unread {
  position: relative !important;
  top: -0.40em !important;
  right: -.2em !important;
}

.file-wrapper a:hover{
  text-decoration: none;
}

.download-folder > button {
  font-size: 0.75rem;
  line-height: 1.33333333;
  border-radius: 3px;
  padding: 0.25rem 0.5rem;
  min-width: 1.625rem;
  border: none;
  box-shadow: none;
  color: #495057ba;
}


.card.drive-folder {
  border: none;
}

.card.drive-folder .card-footer{
  border: none;
  padding: 0;
}
.card.drive-folder .card-footer .download-folder{
  float: right;
  color: #767779ba
}
.card.drive-folder .card.file-wrapper{
  margin: 0;
  border: none;
  padding-bottom: 0 !important;
}

.drive-folder a.dropdown-item{
  cursor: pointer;
}

.footer-container {
  float: right;
}
